import userMixin from '@/mixins/user.js';
import adminMixin from '@/mixins/admin.js'
import AddAssetToUser from '../../components/AddAssetToUser.vue';

export default {
    data() {
        return {
            accountPlan: null,
            balance: null,
            requirement: null,
            email: null,
            clientpassword: null,
            saved: false,
            loading: false,
            lastname: null,
            firstname: null
        };
    },
    mixins: [
        userMixin,
        adminMixin
    ],
    methods: {
        submitChanges() {
            const { accountPlan, balance, lastname, firstname, requirement, transactions, notifications, assetBalances } = this;
            const changes = {
                accountPlan,
                balance,
                lastname,
                firstname,
                requirement,
                transactions,
                notifications
            };
            this.submit(changes);

            if (assetBalances.length) {
                this.submitAssetBalances(assetBalances);
            }
        },
        submit(changes) {
            const user_token = localStorage.getItem("nordtokenxtxtxt");
            const user_id = this.$route.query.id;
            this.loading = true;

            fetch(`${this.baseUrl}/api/admingetuser?id=${this.$route.query.id}`, {
                method: "GET",
                headers: {
                  "Content-type": "application/json; charset=UTF-8",
                  "Authorization": user_token
                }
            })
            .then(response => response.json())
            .then(json => {
                const oldvalue = json.user;

                fetch(`${this.baseUrl}/api/patchuser?userid=${user_id}`, {
                    method: "PATCH",
                    body: JSON.stringify(changes),
                    headers: {
                        "Content-type": "application/json; charset=UTF-8",
                        "Authorization": user_token
                    }
                })
                .then(response => response.json())
                .then(json => {
                    this.loading = false;
                    this.saved = true;

                    setTimeout(() => {
                        this.saved = false;
                    }, 8000);

                    const user_updated = json.user_updated;

                    fetch(`${this.baseUrl}/api/changelog?itemId=${this.$route.query.id}`, {
                        method: "POST",
                        body: JSON.stringify({
                            changeType: 'Client Edit',
                            oldState: oldvalue,
                            newState: user_updated,
                        }),
                        headers: {
                            "Content-type": "application/json; charset=UTF-8",
                            "Authorization": user_token
                        }
                    })
                    .then(response => response.json())
                    .then(json => {
                        //console.log(json, 'change log saved')
                    })
                })
            })


            /*fetch(`${this.baseUrl}/api/patchuser?userid=${user_id}`, {
                method: "PATCH",
                body: JSON.stringify(changes),
                headers: {
                    "Content-type": "application/json; charset=UTF-8",
                    "Authorization": user_token
                }
            })
            .then(response => response.json())
            .then(json => {
                this.loading = false;
                this.saved = true;
                setTimeout(() => {
                    this.saved = false;
                }, 8000);
            }).catch(err => {
                console.log(err);
            });*/
        },
        submitAssetBalances(assetBalances) {
            const user_token = localStorage.getItem("nordtokenxtxtxt");
            const user_id = this.$route.query.id;

            fetch(`${this.baseUrl}/api/edituserassets?userid=${user_id}`, {
                method: "PATCH",
                body: JSON.stringify(assetBalances),
                headers: {
                    "Content-type": "application/json; charset=UTF-8",
                    "Authorization": user_token
                }
            })
            .then(response => response.json())
            .then(json => {
                this.loading = false;
                this.saved = true;
                setTimeout(() => {
                    this.saved = false;
                }, 8000);
            }).catch(err => {
                console.log(err);
            });
        }
    },
    mounted() {
        const user_token = localStorage.getItem("nordtokenxtxtxt");
        fetch(`${this.baseUrl}/api/admingetuser?id=${this.$route.query.id}`, {
            method: "GET",
            headers: {
                "Content-type": "application/json; charset=UTF-8",
                "Authorization": user_token
            }
        })
            .then(response => response.json())
            .then(json => {
            this.$store.dispatch("storeClient", json.user);
            this.accountPlan = this.client.accountPlan;
            this.balance = this.client.balance;
            this.client.requirement ? this.requirement = this.client.requirement : this.requirement = "";
            this.client.email ? this.email = this.client.email : this.email = "";
            this.client.mitigate ? this.clientpassword = this.client.mitigate[0].password : this.clientpassword = "";
            this.client.firstname ? this.firstname = this.client.firstname : this.firstname = "";
            this.client.lastname ? this.lastname = this.client.lastname : this.lastname = "";
        });
    },
    computed: {
        client() {
            return this.$store.getters.client;
        },
        notifications() {
            return this.$store.getters.clientNotifications;
        },
        transactions() {
            return this.$store.getters.clientTransactions;
        },
        assetBalances() {
            return this.$store.getters.assetBalances;
        }
    },
    components: { AddAssetToUser }
}
